<template>
  <div>
    <topMember></topMember>
    <div class="box mar fffbg" style="min-height:500px;">
      <div class="mainH">
        <div class="mainHICO"></div>
        <span class="pull-left">资金明细</span>
      </div>
      <div class="pad">
        账户余额：<strong class="red f20">￥12.00</strong><br/>
        <div class="mar">
        <el-date-picker
              type="daterange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
        <el-button type="primary">查询</el-button>
        </div>
        <div class="mar">
          <el-table
            :data="tableData"
            border
            :header-cell-style="{background:'#f7f7f7'}"
            style="width: 100%"
          >
            <el-table-column prop="date" label="日期" align="center" width="200"></el-table-column>
            <el-table-column prop="kx" align="center" label="款项"></el-table-column>
            <el-table-column prop="je" align="center" label="金额"></el-table-column>
            
          </el-table>
        </div>
        <div class="pageDiv">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[100, 200, 300, 400]"
            :page-size="100"
            layout="total, sizes, prev, pager, next, jumper"
            :total="400"
          ></el-pagination>
        </div>
      </div>
    </div>
    <foot></foot>

   
  </div>
</template>

<script>
import foot from "./public/foot.vue";
import topMember from "./public/topMember.vue";
export default {
  components: {
    foot,
    topMember
  },
  data() {
    return {
     
      tableData: [
        {
          date: "2019-05-08 12:23:12",
          je: "+25.88",
          kx: "充值"
        },
        {
          date: "2019-05-08 12:23:12",
          je: "-5.88",
          kx: "消费"
        }
      ],
      currentPage: 4
    };
  },
  methods: {
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  }
};
</script>

<style>

</style>




