     sendb2(){
       this.tab2step2=false;
       this.tab2step1=true;
     },
     sendb3(){
       this.tab2step2=false;
       this.checkProduct=true;
        this.activeStep=2
     },
     type3send(){
        this.checkProduct=true;
        this.activeStep=2;
        this.tab3step=false;
     }
  }
};
</script>

