<template>
  <div>
    <top></top>
    <div class="box">
        <div class="loginCenter">
             <div class="loginBg">
                <div class="loginTitle">欢迎来到礼品淘</div>
                <div class="loginDesc">我们提供赠品采购、发货、一站式服务。</div>
                <div class="inp1"><el-input prefix-icon="el-icon-mobile" placeholder="请输入手机号" v-model="mobile"> </el-input></div>
                
                <div class="inp1"><el-input prefix-icon="el-icon-lock" show-password placeholder="请输入密码" v-model="password"> </el-input></div>
               
                <el-row :gutter="20" style="margin-top:15px;">
                     <el-col :span="12"><el-button @click="login" class="submitBt" type="primary">登录</el-button></el-col>
                     <el-col :span="12"><el-button @click="register" class="submitBt" type="info">注册</el-button></el-col>
                </el-row>  
             </div>  
        </div>  
    </div>
    <div style="position:fixed;left:0;bottom:0;width:100%;"><foot></foot></div>
  </div>
</template>

<script>
import foot from './public/foot.vue'
import top from './public/top.vue'
export default {
  components: {
    foot,
    top
  },
  data() {
    return {
      mobile:'',
      password:''
    };
  },
  methods:{
    login(){
      this.$router.push('/register')
    },
    register(){
      this.$router.push('/register')
    }
  }
};
</script>

<style scoped>

.loginCenter {
  background:url(../../static/loginLeft.png) no-repeat 40px center;
  height:533px;
  position:absolute;
  top:50%;
  margin-top:-266px;
  width:1200px;
  left:50%;
  margin-left:-600px;
}
.loginBg {
  background:#fff;
  border-radius: 10px;
  padding:25px;
  float:right;
  margin-top:20px;
  margin-right:20px;
  width:320px;
}
.loginTitle {
  font-size:24px;
}
.loginDesc {
  font-size:15px;
  color:#999;
}
.inp1 {
  margin-top:15px;
  position:relative;
}
.getCode {
  position:absolute;
  right:20px;
  top:0px;
}
.submitBt {
  width:100%;
}

</style>



